import React, { useState } from 'react';

const MOCK_COURSES = [
  { id: '1', code: 'CS-302', name: 'Advanced Database Systems', instructor: 'Dr. Sarah Jenkins', students: 45, status: 'Active', icon: 'database', color: 'bg-blue-100 text-blue-600', avatars: ['https://i.pravatar.cc/150?u=10', 'https://i.pravatar.cc/150?u=11', 'https://i.pravatar.cc/150?u=12'] },
  { id: '2', code: 'CS-101', name: 'Intro to Computer Science', instructor: 'Prof. Alan Turing', students: 120, status: 'Active', icon: 'terminal', color: 'bg-indigo-100 text-indigo-600', avatars: ['https://i.pravatar.cc/150?u=20'] },
  { id: '3', code: 'MKT-205', name: 'Digital Marketing Strategy', instructor: 'Emily White', students: 30, status: 'Active', icon: 'campaign', color: 'bg-orange-100 text-orange-600', avatars: ['https://i.pravatar.cc/150?u=30', 'https://i.pravatar.cc/150?u=31'] },
  { id: '4', code: 'ENG-400', name: 'Structural Engineering', instructor: 'Robert Chen', students: 85, status: 'Archived', endDate: 'Dec 2023', icon: 'architecture', color: 'bg-slate-100 text-slate-600', avatars: [] },
  { id: '5', code: 'DES-110', name: 'UX/UI Design Principles', instructor: 'Alice Wonder', students: 64, status: 'Active', icon: 'design_services', color: 'bg-purple-100 text-purple-600', avatars: ['https://i.pravatar.cc/150?u=40', 'https://i.pravatar.cc/150?u=41'] },
];

const AdminCourses: React.FC = () => {
  const [filter, setFilter] = useState('All');
  const [search, setSearch] = useState('');

  const filteredCourses = MOCK_COURSES.filter(course => {
    // Mock filter logic - in real app would filter by department/category
    const matchesSearch = course.name.toLowerCase().includes(search.toLowerCase()) || 
                          course.code.toLowerCase().includes(search.toLowerCase());
    return matchesSearch;
  });

  return (
    <div className="flex flex-col h-full bg-background-light dark:bg-background-dark">
      {/* Header */}
      <div className="p-4 bg-white dark:bg-slate-800 shadow-sm z-10 sticky top-0">
        <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-2">
                <button className="text-slate-900 dark:text-white">
                    <span className="material-symbols-outlined">arrow_back_ios_new</span>
                </button>
                <h1 className="text-xl font-bold text-slate-900 dark:text-white">Courses</h1>
            </div>
          <button className="text-primary hover:bg-blue-50 p-2 rounded-full transition-colors">
            <span className="material-symbols-outlined">add</span>
          </button>
        </div>

        {/* Search */}
        <div className="relative mb-4">
          <span className="material-symbols-outlined absolute left-3 top-1/2 -translate-y-1/2 text-slate-400">search</span>
          <input 
            type="text" 
            placeholder="Search by name or code..." 
            className="w-full h-11 pl-10 pr-4 rounded-xl bg-slate-50 dark:bg-slate-700 border-none focus:ring-2 focus:ring-primary text-slate-900 dark:text-white"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>

        {/* Filters */}
        <div className="flex gap-2 overflow-x-auto no-scrollbar pb-1">
          {['All', 'Computer Science', 'Business', 'Engineering', 'Design'].map((f) => (
            <button
              key={f}
              onClick={() => setFilter(f)}
              className={`px-4 py-2 rounded-full text-sm font-medium whitespace-nowrap transition-colors ${
                filter === f 
                  ? 'bg-primary text-white' 
                  : 'bg-white dark:bg-slate-700 text-slate-600 dark:text-slate-300 border border-slate-200 dark:border-slate-600'
              }`}
            >
              {f}
            </button>
          ))}
        </div>
      </div>

      {/* Course List */}
      <div className="p-4 flex flex-col gap-3 pb-24 overflow-y-auto">
        {filteredCourses.map(course => (
          <div key={course.id} className="bg-white dark:bg-slate-800 p-4 rounded-xl shadow-sm flex flex-col gap-3">
            <div className="flex justify-between items-start">
                <div className="flex gap-4">
                    <div className={`h-12 w-12 rounded-xl flex items-center justify-center ${course.color}`}>
                        <span className="material-symbols-outlined text-2xl">{course.icon}</span>
                    </div>
                    <div>
                        <div className="flex items-center gap-2 mb-1">
                            <span className="text-[10px] font-bold bg-blue-50 text-blue-600 px-1.5 py-0.5 rounded uppercase">{course.code}</span>
                            {course.status === 'Active' ? (
                                <span className="flex items-center gap-1 text-[10px] font-bold text-green-600 bg-green-50 px-1.5 py-0.5 rounded uppercase">
                                    <span className="w-1.5 h-1.5 rounded-full bg-green-500"></span> Active
                                </span>
                            ) : (
                                <span className="text-[10px] font-bold text-slate-500 bg-slate-100 px-1.5 py-0.5 rounded uppercase">
                                    Archived
                                </span>
                            )}
                        </div>
                        <h3 className="text-base font-bold text-slate-900 dark:text-white leading-tight">{course.name}</h3>
                        <div className="flex items-center gap-1 mt-1">
                            {/* Simple avatar placeholder for instructor */}
                            <div className="w-4 h-4 rounded-full bg-slate-200 overflow-hidden">
                                <img src={`https://ui-avatars.com/api/?name=${course.instructor}&background=random`} alt="" />
                            </div>
                            <span className="text-sm text-slate-500 dark:text-slate-400">{course.instructor}</span>
                        </div>
                    </div>
                </div>
                <button className="text-slate-400">
                    <span className="material-symbols-outlined">more_horiz</span>
                </button>
            </div>

            <div className="h-px w-full bg-slate-100 dark:bg-slate-700"></div>

            <div className="flex justify-between items-center">
                <div className="flex -space-x-2">
                    {course.avatars.map((avatar, i) => (
                        <img key={i} src={avatar} alt="" className="w-6 h-6 rounded-full border-2 border-white dark:border-slate-800" />
                    ))}
                    {course.students > course.avatars.length && (
                        <div className="w-6 h-6 rounded-full bg-slate-100 dark:bg-slate-700 border-2 border-white dark:border-slate-800 flex items-center justify-center text-[8px] font-bold text-slate-500">
                            +{course.students - course.avatars.length}
                        </div>
                    )}
                </div>
                
                {course.status === 'Archived' && course.endDate ? (
                    <span className="text-xs text-slate-400 italic">Course ended: {course.endDate}</span>
                ) : null}

                <span className="text-xs font-medium text-blue-600">{course.students} Students</span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AdminCourses;
